  font-family: 'WorkSansRegular';
  src: local('WorkSansRegular'), url(/met3rv2/static/media/WorkSans-Regular.7bb60624.woff) format('woff');
  src: local('WorkSansRegular'), url(/met3rv2/static/media/WorkSans-Regular.86ac3dee.ttf) format('truetype'); 
